import './main.css';
import img1 from './images/img1.png';
import img2 from './images/gp1-2.png';
import img3 from './images/pg1-3.png';
import img4 from './images/p-4.png';
import img5 from './images/p5.png';
import img6 from './images/p6.png';
import img7 from './images/p7.png';

function Gallery() {
    return (
        <>
        <div className="container">
            <div id="gallary">
                <div className="row">

                    <div className="col-sm-12">
                        <h2 style={{color:'#AD7835'}} ><u>Ga</u>llary</h2>
                    </div>
                </div>
                <div id="ga" align="center">
                    <div className="row">
                        <div className="col-sm-4">
                            <img src={img1} className="img-responsive" id="g1"/>
                        </div>
                        <div className="col-sm-3">
                            <img src={img2} className="img-responsive" id="g2"/>

                        </div>
                        <div className="col-sm-3">
                            <img src={img3} className="img-responsive" id="g3"/>

                        </div>
                    </div>
                    <div className="row">
                        <div className="col-sm-4">
                            <img src={img4} className="img-responsive" id="g4"/>
                        </div>
                        <div className="col-sm-6">
                            <img src={img5} className="img-responsive" id="g5"/>

                        </div>
                    </div>

                        <div className="row">
                            <div className="col-sm-4"></div>
                                <div className="col-sm-3">
                                    <img src={img6} className="img-responsive" id="g6"/>

                                </div>
                                <div className="col-sm-3">
                                    <img src={img7} className="img-responsive" id="g7"/>

                                </div>
                        </div>

                    <div className="row">
                        <div className="col-sm-12">
                            <br/><br/>
                        <a href="#"  id="show" style={{color:'#AD7835',textDecoration:'none' ,alignItems:'center'}}> SHOW MORE</a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
            <br/><br/>

        </>
    );
}
export default Gallery;