
import {FaFonticons, FaTwitter} from "react-icons/fa";
import {faFontAwesome} from "@fortawesome/free-brands-svg-icons";


function Icon(){
    return(
        <>
            <div className="container">
                <div className="row" align="center">
                    <div>
                        <div className="col-sm-12" id="ic">



                            <FaFonticons icon={["fal", "twitter"]} size="" style={{color:'white'}}/>

                        </div>
                    </div>
                </div>
            </div>
        </>
    );

}
export default Icon;
