import './main.css';
import ph from './images/ph-1.png';

function Contact() {
    return (
        <>
            <div className="container">

                <div className="row" id="contact">
                    <div className="col-sm-2"></div>
                    <div className="col-sm-4">
                        <img src={ph} id="cimg" className="img-responsive"/>
                    </div>

                    <div className="col-sm-6">
                        <h1 style={{ color:'#AD7835', }}><u>Con</u>tact</h1>
                        <h6 style={{color: 'white'}}>your name</h6>
                        <input type="text"/>
                        <h6 style={{color:' white',}}>your email</h6>
                        <input type="text"/><br/>
                        <br/>
                        <textarea cols="20" rows=" 5"></textarea><br/><br/>
                        <a href="#" id="sub">SEND MESSAGE</a>
                    </div>

                </div>

            </div>
        </>
    );
}

export default Contact;